.stand {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 80%;
    margin: 0 auto;
}

.inputs {
    display: flex;
    flex-direction: column;
    gap: 35px;
}

.buttons {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.checkboxes {
    display: flex;
    flex-direction: column;
    gap: 36px;
}
